.search {
    color:  black;
    height: 100vh;
    width:  100%;
    background: #fcfafd;
}

.search img {
    width: 30%;
    height: 50%;
}

input {
    outline: none;
    margin-right: 5px;
    padding: 3px;
    border: .5px solid gray;
    border-radius: 4px;
}

input:focus {
    -webkit-transition: ease-in-out;
    -o-transition: ease-in-out;
    transition: ease-in-out;
    -webkit-transition-duration: .15s;
         -o-transition-duration: .15s;
            transition-duration: .15s;
    -webkit-box-shadow: 1px 1px 6px 1px rgba(128, 128, 128, 0.664) inset;
            box-shadow: 1px 1px 6px 1px rgba(128, 128, 128, 0.664) inset;
}

.form button {
    padding: 4px;
}

select {
    outline: none;
    margin-right: 5px;
    margin-left: 5px;
    margin-top: 4px;
}

.form {
    margin-top: 10px;
}
.form div {
    padding-right: 5px;
    font-size: .75em;
}
.list-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    margin-top: 15px;
}

.search-list {
    width: 40%;
    padding: 20px;
}


.list-gear {
    background: rgba(255, 255, 255, 0.927);
    padding: 5px;
    margin: 5px;
    border-radius: 5px;
    -webkit-box-shadow: 0 10px 20px 0 rgba(0,0,0,0.2), 0 6px 6px 0 rgba(0,0,0,0.2);
            box-shadow: 0 10px 20px 0 rgba(0,0,0,0.2), 0 6px 6px 0 rgba(0,0,0,0.2);
}

.list-gear a {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
}

.itemname {
    width: 40%;
}
.list-gear:hover {
    font-weight: 800;
}

a {
    text-decoration: none;
    color: black;
}

.list-gear img {
    height: 60px;
    width: 60px;
}

.listimage {
    border-radius: 50%;
    width: 85px !important;
    height: 85px !important;
}

.list-gear img:hover {
    -webkit-transition: ease-in-out;
    -o-transition: ease-in-out;
    transition: ease-in-out;
    -webkit-transition-duration: .5s;
         -o-transition-duration: .5s;
            transition-duration: .5s;
    width: 100px !important;
    height: 100px !important;
}

@media (min-width: 1500px) {
    .search img {
        width: 25%;
    }
}
@media (min-width: 1300px) {
    .search img {
        width: 28%;
    }
}


@media (max-width: 900px) {
    .search img {
        width: 30%;
        height: 24%;
    }

    .form {
        margin: 10px auto;
        width: 55%;
    }
    .form input {
        margin-top: 10px;
    }
}
@media (max-width: 600px) {
    .search img {
        width: 25%;
        height: 14%;
    }

    .form {
        margin: 10px auto;
        width: 55%;
    }
    .form input {
        margin-top: 10px;
    }
}


